.navbar {
  height: 70px;
}
.navbar-collapse {
  justify-content: flex-end;
}

.header-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
.header-logo:hover {
  text-decoration: none;
}
.header-logo-icon {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: 4px solid #4682B4;
  background-color: transparent;
  margin-right: 15px;
}
.header-logo-text {
  color: #5F9EA0;
  font-family: "Lato", sans-serif;
  font-size: 22px;
  font-weight: bold;
}

.nav-item {
  margin: 0 5px;
}

.article-wrap {
  margin-top: 120px;
  padding: 0 15px;
}
.article-title {
  font-weight: 600;
}
.article-data {
  margin: 20px 0 30px 0;
  font-size: 14px;
}
.article-author {
  margin: 0;
}
.article-date {
  color: #999999;
}

@media (min-width: 768px) {
  .article-wrap {
    padding-left: 0;
  }
}
.sidebar-wrap {
  margin-top: 60px;
  padding: 0 15px;
}
.sidebar-wrap .sidebar-title {
  margin-bottom: 20px;
}
.sidebar-wrap .sidebar-element:hover {
  opacity: 0.8;
}
.sidebar-wrap .sidebar-element:hover .figure-caption {
  opacity: 0.6;
}
.sidebar-wrap .figure-caption {
  margin-bottom: 10px;
  color: inherit;
  font-weight: bold;
}

@media (min-width: 768px) {
  .sidebar-wrap {
    padding-right: 0;
    margin-top: 130px;
  }
}
.article-comments {
  margin-top: 45px;
}
.article-comments form .form-row-last {
  display: flex;
  justify-content: flex-end;
}
.article-comments form .btn {
  margin-top: 10px;
  border-radius: 15px;
  padding: 6px 20px;
  text-transform: uppercase;
}
.article-comments form .btn:focus {
  outline: none;
  box-shadow: none;
}
.article-comments .row-comment {
  margin-top: 20px;
}
.article-comments .row-comment .comment {
  margin: 10px 0;
  width: 100%;
}
.article-comments .row-comment .comment-header {
  margin-bottom: 10px;
}
.article-comments .row-comment .comment-author {
  font-weight: bold;
}
.article-comments .row-comment .comment-date {
  color: #999999;
  margin-left: 20px;
}
.article-comments .row-comment .comment-footer {
  color: #999999;
}
.article-comments .row-comment .btn {
  color: #999999;
}
.article-comments .row-comment .btn:hover {
  color: #212529;
}

.footer {
  margin-top: 20px;
  font-size: 14px;
}
.footer-container {
  border-top: 1px solid #ececec;
  padding: 20px;
}
.footer-column-title {
  margin-top: 20px;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: inherit;
  text-transform: uppercase;
}
.footer-column-list {
  list-style: none;
  line-height: 1.8;
  font-size: inherit;
  padding: 0;
}
.footer-column-list-link {
  text-decoration: none;
}
.footer-column-list-link:hover {
  opacity: 0.8;
  text-decoration: none;
}
.footer-column-text {
  font-size: 12px;
}
.footer-column-social {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
}
.footer-column-social i {
  padding: 10px;
  color: #212529;
}
.footer-column-social i:hover {
  opacity: 0.8;
}
.footer .copyright {
  margin-top: 10px;
  font-size: 12px;
  color: #999999;
  text-align: center;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Open Sans", sans-serif;
}

.container {
  padding: 0;
}

.row {
  margin: 0;
}

@media (max-width: 768px) {
  body {
    font-size: 1.2em;
  }
}